.form-field {
  display: block;
  width: 100%;
  height: 48px;
  padding: 14px 16px;
  background-color: #f8f8fa;
  border: 1px solid transparent;
  border-radius: 40px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #292929;

  &::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #7e7e83;
  }
  &:hover {
    border-color: #d5d7de;
  }

  &:focus {
    border-color: $c-gray-darc;
    outline: none;
  }

  &--textarea {
    font-family: inherit;
    height: 144px;
    resize: none;
    border-radius: 16px;
  }

  &:-webkit-autofill,
  &:-webkit-autofill:focus {
    transition: background-color 600000s 0s, color 600000s 0s;
  }
}
